.container {
  width: 970px;
  margin: 0 auto;
  padding: 0 20px; }

.invisible {
  visibility: hidden; }

.screen-readers-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

body {
  min-width: 20rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: calc(10px + 1vmin);
  line-height: 23px;
  font-weight: 400;
  color: #343434;
  text-align: center;
  background-color: #f1f1f1; }

.btn {
  display: inline-block;
  padding: 5px 20px;
  border: 1px solid black;
  border-radius: 100px;
  font-size: calc(10px + 1vmin);
  font-weight: 700;
  text-transform: uppercase;
  color: #222222;
  background-color: #ebebeb;
  cursor: pointer; }

.fade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.title {
  font-size: calc(12px + 2vmin);
  line-height: 28px;
  font-weight: 400; }

.main {
  display: flex;
  border: 6px solid brown; }

.menu {
  position: relative;
  min-height: 100vh;
  width: 20%;
  box-shadow: 6px 0px 0px 0px brown; }
  .menu__title {
    padding: 20px; }
  .menu__subtitle {
    margin-bottom: 50px;
    padding: 0 10px; }
  .menu__btn {
    width: 80%;
    margin-bottom: 10px; }
    .menu__btn--animation {
      margin-top: 10px;
      width: 100px;
      height: 100px;
      border-radius: 50%; }
      .menu__btn--animation:hover {
        border-width: 3px; }
    .menu__btn:hover {
      border-color: brown;
      color: brown; }
  .menu__modal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #f1f1f1; }
  .menu__form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
    margin-top: 40px; }
  .menu__input {
    width: 30px;
    height: 30px;
    margin: 0 10px;
    padding-left: 10px;
    padding-bottom: 5px;
    font-size: inherit; }
    .menu__input--save {
      width: 200px;
      margin-bottom: 30px; }
  .menu__list {
    list-style: decimal;
    margin: 30px; }
  .menu__item {
    padding: 10px 50px;
    border: 1px solid lightgray;
    cursor: pointer; }
    .menu__item:hover, .menu__item:focus {
      background-color: azure; }

.game {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%; }

.table {
  padding: 30px;
  margin: 100px;
  display: table;
  margin-bottom: 10px;
  border-collapse: collapse; }
  .table__row {
    display: table-row; }
  .table__cell {
    display: table-cell;
    width: 50px;
    height: 50px;
    border: 1px solid black;
    background-color: lightgray; }
    .table__cell:hover {
      background-color: lightgreen; }
    .table__cell--green {
      background-color: lightgreen; }
